@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.about{
overflow: hidden;
}
.img-writes1{
    display:flex ;
    overflow: hidden;
}

.col1{
    margin-top: 20px;
    flex-basis: 50%;
}
.col1 img{
    margin-left: 20vh;
    height: 60vh;
  
   
}
.col1 h2 {
    padding-top: 10vh;
    /* margin: 10px 0; */
    font-size: 1.2rem;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}
.col1 p {
    margin-top: 2vh;
    font-size: 0.8rem;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}
.aboutconatainer{
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
}
.aboutconatainer .box p{
    font-size: 0.8rem;font-weight: 400;
}

@media(max-width: 850px){
    .img-writes1{
        flex-direction: column;
    }
    .col1 img{
        margin-left: 10vh;
         height: 40vh;
   
    }

}
@media(max-width: 450px){
    .col1 img{
        margin-left: 4vh;
        height: 35vh;
   
    }
    .aboutconatainer{
        display: grid;
        gap: 15px;
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
    .aboutconatainer .box h2{
        font-size: 1rem;
        
    }
    .aboutconatainer .box p{
        font-size: 0.8rem;
      
    }
}