.main{
    padding: 10px;
    height: 100vh;
   
}

.layout{
    display: flex;
   
}

.sidebar{
  
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: rgb(51, 1, 1);
    box-shadow: 0 0 2px gray; 
    margin-right:20px;
    color: white;
   
}



.content{ 
width: 100%;
height: 100%;
overflow: auto;

}

.header{
position: sticky;    
height: 10vh;
margin-bottom: 20px;
box-shadow: 0 0 2px gray;
background-color: white;
}

.body{
  
height: 85vh;
margin-bottom: 20px;
box-shadow: 0 0 2px gray;
background-color: white;
}
.name{
    font-size: 5rem;
}

.logo h5{
    font-size: 1.8rem;
    text-align: center;
    margin: 10px 0;
}

.menu{
    margin-top: 50px;
   
}

.menu-item{
    margin-top: 30px;
}

.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
}

.active{
   
    background-color: white;
    padding: 10px;
}

.active a , .active i{
    color:  rgb(51, 1, 1);
}

.header-content{
   
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}

.header-content i{
    margin-right: 10px;
    font-size: 1.3rem;
}

.header-content a{
    text-decoration: none;
    font-size: 1.2rem;
    color: blue;
    margin: 0 10px;
    text-transform: uppercase;
}


@media(max-width: 500px){
   
    .menu-item a{
        font-size: 1rem;
    }
    .sidebar{
        width: 200px;
    }
    .readNotification{
        font-size: 1rem;
    }
    .unreadNotification{
        font-size: 1rem;
    }
    .homepage-heading,.applyteacher-heading,.appoinment-heading,.booking-heading,.notes-heading{
        font-size: 1.5rem;
    }
    .logo h5{
        font-size: 1.2rem;
    }
    .header-content .name{
        font-size: 1rem;
    }
   
}
@media(max-width: 350px){
    .header-content .name{
        font-size: 0.8rem;
    }
}