
  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header1{
    background-color:  rgb(51, 1, 1);
    height: 75px;
    padding: 10px 0;
    min-height: 100%;
    
} 
.header1 .logo{
    float: left;
    color: white;
    font-size: 1.5rem;
    padding-left: 10px;
    margin: 10px 0;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;  
}
.header1 .logo .Link{
    text-decoration: none;
    color: white;
}
.menu{
    margin: 0;
}
 .header1 .Link{
    margin: 0;
    float: right;
   cursor: pointer;
}

.main1{

    background-color: white;
    min-height: 70vh;
    align-items: center;
}
.img-writes{
    display:flex ;
    overflow: hidden;
}

.col{
    margin-top: 20px;
    flex-basis: 50%;
}
.col img{
    margin-left: 10vh;
    width: 70vh;
   
}
.col h2 {
    padding-top: 10vh;
    /* margin: 10px 0; */
    font-size: 1.2rem;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}
.col p {
    margin-top: 2vh;
    font-size: 0.8rem;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}
.conatainer{
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
}
 .box{
    margin-bottom: 30px;
    padding: 20px 10px;
    text-align: center;
    border-radius: 5px;
    background: white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.box h2{
    font-family:'Poppins', sans-serif ;
    color: #444;
    padding: 10px 0;
    font-size: 1.5rem;
}
.box h3{
    font-family:'Poppins', sans-serif ;
    color: #444;
    padding: 10px 0;
    font-size: 1rem;
}
.box p{
    color: #777;
    font-size: 1rem;
    line-height: 1.6;
    font-weight: 200;
}

.teachers{
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
}
.teachers .box{
    margin-bottom: 30px;
    padding: 20px 10px;
    text-align: center;
    border-radius: 5px;
    background: white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.teachers .box h2{
    font-family:'Poppins', sans-serif ;
    color: #444;
    padding: 10px 0;
    font-size: 1.5rem;
}
.box h3{
    font-family:'Poppins', sans-serif ;
    color: #444;
    padding: 10px 0;
    font-size: 1rem;
}
 .teachers .box p{
    font-family:'Poppins', sans-serif ;
    color: #777;
    font-weight: bold;
    font-size: 0.8rem;
    line-height: 1.6;
   

}


.footer{
    background-color:  rgb(51, 1, 1);
    height: 140px;
}
.footer h4{
    color: white;
    /* padding: 10 0px; */
    margin-top: 20px;
    font-weight: 20;
    font-family: 'Poppins', sans-serif;
}
.footer h3{
    color: white;
    padding: 0 10px;
    font-weight: 50;
    font-family: 'Poppins', sans-serif;
}
.footer .contect{
    text-align: center;
    cursor: pointer;
}

.footer .contect .fa-brands{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    line-height: 40px;
    font-size: 20px;
    color: #000;
    background-color: #fff;
    text-align: center;
    justify-content: center;
    margin-right: 15px;
    

}
@media(max-width: 1000px){
    .col img{
        width: 50vh;
    }
}
@media(max-width: 830px){
    .img-writes{
        flex-direction: column;
    }}

@media(max-width: 700px){
   
    .header1 .logo .Link  {
        font-size: 1.2rem ;
    } 
    .footer h4{
        font-size: 0.8rem;
    }
    .footer h3{
        font-size: 1rem;   
    }
    .header1 .Link{
        font-size: 0.8rem;
    }
    .img-writes{
        flex-direction: column;
    }
    .col img{
        margin-left: 0;
        width: 40vh;
    }
}
@media(max-width: 359px){
    .header1 .logo .Link  {
        font-size: 0.8rem ;
    }
    .header1 .Link{
        font-size: 0.6rem;
    }   
}